/* Dark mode changes */
[data-md-color-scheme="slate"] {
  .md-typeset table:not([class]) {
    box-shadow: var(--md-shadow-z2);

    tr:hover {
      background-color: rgba(0,0,0,.08);
    }

    th {
      color: var(--md-text-color);
      background-color: var(--md-primary-fg-color);
      border-bottom: px2rem(1px) solid var(--md-accent-fg-color);
    }

    td {
      border-top: px2rem(1px) solid var(--md-default-fg-color--lighter);
    }
  }
}

.md-typeset table:not([class]) {
  tr.table-container {
    visibility: visible;
  }

  tr.table-container:not([open]) {
    visibility: collapse;
  }

  tr.table-container-ctrl {
    cursor: pointer;
    > td:first-child {
        &::after {
          background-color: var(--md-accent-fg-color);
          content: "";
          height: 1rem;
          -webkit-mask-image: var(--md-details-icon);
          mask-image: var(--md-details-icon);
          -webkit-mask-position: center;
          mask-position: center;
          -webkit-mask-repeat: no-repeat;
          mask-repeat: no-repeat;
          -webkit-mask-size: contain;
          mask-size: contain;
          position: absolute;
          //top: .625em;
          transform: rotate(0deg);
          transition: transform .25s;
          width: 1rem;
      }
    }

    &:hover {
      box-shadow: inset 0 0 px2rem(6px) var(--md-accent-fg-color);
    }

    &[open] > td:first-child::after {
      transform: rotate(90deg);
    }
  }

  & caption {
    padding-top: px2rem(6px);
    padding-bottom: px2rem(6px);
    font-weight: bold;
  }
}

.md-typeset .table-container table:not([class]) {
  box-shadow: none;
  margin: 0;
}